import React  from 'react';
import Track  from './Track';
import {Link} from 'react-router-dom';

const TrackList = (props) => {
    
    
    let tracksMapped = props.tracks.map((item, index) => {
       
        return <Track showMoreFN = {props.showMoreFN} name = {item.name} artist = {item.artist.name} showMore = {false} index = {index} playcount = {item.playcount} listeners = {item.listeners}/> 
    })
    
    return (
        <div className = 'track-list'>
            {tracksMapped}
        </div>
    )
}

export default TrackList; 


