import React from 'react';
import Album from './Album';

const AlbumGrid = (props) => {

  
    const albumsMapped = props.albums.map(item => {
        return <Album {...item} />
        
    })
    
    return (
        <div className = 'album-grid'>
            {albumsMapped}
        </div>
        )
}

export default AlbumGrid;




