import React from 'react';

const Playback = () => {
    return (
        <div className = 'playback'>
            
            <div className = 'playback__controls-left'>
            
            </div>
            
            <div className = 'playback__controls-center'>
                <a className = 'playback__btn-small'>
                    <i class="fas fa-step-backward"></i>
                </a>
                
                <a className = 'playback__btn'>
                    <i className="fas fa-play"></i>
                </a>
                
                <a className = 'playback__btn-small'>
                    <i className="fas fa-step-forward"></i>
                </a>
            </div>
            
            <div className = 'playback__controls-right'>
               
            </div>
            
        </div>
    )
}

export default Playback;


