import React  from 'react';
import {Link} from 'react-router-dom';

const Tile = (props) => {
    return(
        <Link to = {'/artist/' + props.name} className = 'tile'>
            <img src = {props.img} />
            <p>{props.name}</p>
        </Link>
    )
}

export default Tile


