import React  from 'react';
import {Link} from 'react-router-dom';

const TagGrid = (props) => {
    
    const tagsMapped = props.tags.map(tag => {
        return <Link to = {'/tag/' + tag.name} className = 'tag'>{tag.name}</Link>
    })

    return (
        <div className = 'tag-grid'>
            {tagsMapped}
        </div>
    )
}

export default TagGrid;


