import React, {Component} from 'react';
import {Link}             from 'react-router-dom';

class Track extends Component {
   constructor(props) {
       super(props)
       this.state = {
           showMore: false
       }
   }
   
   showMore() {
       this.setState({
           showMore:!this.state.showMore
       })
   }
   
    render() {
        return(
        <div className = 'track'  >
        
            <p className = 'track__name'>{this.props.index + 1}. {this.props.name}</p>
            
            <button className = {!this.state.showMore ? 'btn-sm btn-more' : 'btn-sm btn-less'} onClick = {this.showMore.bind(this)}>{this.state.showMore? 'Show Less' : 'Show More'}</button>
            
            <div className = {this.state.showMore ? 'track__info show-more' : 'track__info show-less'}>
                {this.state.showMore ? <div>
                    <Link className = 'track__link' to = {'/artist/' + this.props.artist}><i class="far fa-user"></i>  {' ' + this.props.artist}</Link>
                    <p>Playcount:  {this.props.playcount}</p>
                    <p>Listeners:  {this.props.listeners}</p>
                </div>
                : null }
            </div>
            
        </div>
    )
    }
}

export default Track;


